import React, {useState} from 'react'
import './weather.css'
import axios from 'axios'
import Weatherdata from './weatherdata'

function Weather () {
    const [place,setplace] = useState("")
    const [apidata, setAPIData] = useState({
        cloud: "0",
        humidity: '0',
        temp: '0',
        wind_speed: '0'
    })

    const submit = () => {
        axios.get(`https://api.weatherapi.com/v1/current.json?key=cbbafcb394aa4eb488d53447202311&q=${place}`)
        .then(data1 => {
            const { data:{current}} = data1
            setAPIData({
                cloud:current.cloud,
                humidity:current.humidity,
                temp:current.temp_f,
                wind_speed:current.wind_kph
            })
        })
        .catch(error => {alert("Error Occured in Getting Data check internetðŸ§©ðŸš€ðŸš€ Connection")})
    }

    return (
        <div className="container">
            <div className="weather__title">Weather App</div> 
            <div className="weather__location">
                <h3>Location : {place}</h3>
            </div>    
            <div className="weather__input">
                <input type="text" name="place" onChange={(e) => setplace(e.target.value)}/>
                <button onClick={submit} className="weather__submit">Get Weather</button>
            </div>   
            <div className="weather__weatherdata">
                <Weatherdata APIdata={apidata} />
            </div>
        </div>
    )
}

export default Weather;