import React, { useState } from 'react'

export default function Weatherdata(APIdata) {
    return data_Weather(APIdata)
}
function data_Weather(APIdata) {
    const {APIdata: {cloud, humidity, temp, wind_speed}} = APIdata
    return (
        <div>
            <div style={{marginTop:'50px 0 0 0',backgroundColor:"#12737f"}} className="weatherdata__data">
                <table>
                    <tbody style={{fontSize:'20px',textAlign:"center"}} className="weatherdata__data__body">
                    <tr>
                        <td colSpan="2" style={{fontSize:'30px',textAlign:"center"}} >Weather Data</td>
                    </tr>
                    <tr>
                        <td>Cloud</td>
                        <td>{cloud}</td>
                    </tr>
                    <tr>
                        <td>Humidity</td>
                        <td>{humidity}</td>
                    </tr>
                    <tr>
                        <td>Temperature</td>
                        <td>{temp}F</td>
                    </tr>
                    <tr>
                        <td>Wind Speed </td>
                        <td>{wind_speed}KPH</td>
                    </tr>
                    </tbody>
                </table>
            </div>
        </div>
    )
}

